"use client"

import { useState, useEffect } from 'react'
import { Progress } from '@/components/ui/progress'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'

interface LoadingIndicatorProps {
  title?: string
  description?: string
  estimatedTime?: number
}

export function LoadingIndicator({ 
  title = "Video OluÅŸturuluyor", 
  description = "AI videonuzu hazÄ±rlÄ±yor, lÃ¼tfen bekleyin...",
  estimatedTime = 60
}: LoadingIndicatorProps) {
  const [progress, setProgress] = useState(0)
  const [currentStep, setCurrentStep] = useState(0)
  const [elapsedTime, setElapsedTime] = useState(0)

  const steps = [
    { name: "Prompt analiz ediliyor", duration: 5 },
    { name: "GÃ¶rsel sahneler oluÅŸturuluyor", duration: 25 },
    { name: "Karakter animasyonlarÄ± hazÄ±rlanÄ±yor", duration: 15 },
    { name: "Ses efektleri ekleniyor", duration: 8 },
    { name: "Arka plan mÃ¼ziÄŸi oluÅŸturuluyor", duration: 10 },
    { name: "Video birleÅŸtiriliyor", duration: 12 },
    { name: "Son iÅŸlemler yapÄ±lÄ±yor", duration: 5 }
  ]

  useEffect(() => {
    const interval = setInterval(() => {
      setElapsedTime(prev => prev + 1)
      
      // Calculate progress based on elapsed time
      const progressPercentage = Math.min((elapsedTime / estimatedTime) * 100, 95)
      setProgress(progressPercentage)
      
      // Update current step
      let totalDuration = 0
      for (let i = 0; i < steps.length; i++) {
        totalDuration += steps[i].duration
        if (elapsedTime <= totalDuration) {
          setCurrentStep(i)
          break
        }
      }
    }, 1000)

    return () => clearInterval(interval)
  }, [elapsedTime, estimatedTime])

  const formatTime = (seconds: number) => {
    const mins = Math.floor(seconds / 60)
    const secs = seconds % 60
    return `${mins}:${secs.toString().padStart(2, '0')}`
  }

  const remainingTime = Math.max(estimatedTime - elapsedTime, 0)

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center space-x-2">
          <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-primary"></div>
          <span>{title}</span>
        </CardTitle>
        <CardDescription>{description}</CardDescription>
      </CardHeader>
      <CardContent className="space-y-6">
        {/* Main Progress Bar */}
        <div className="space-y-2">
          <div className="flex justify-between text-sm">
            <span>Ä°lerleme</span>
            <span>{Math.round(progress)}%</span>
          </div>
          <Progress value={progress} className="w-full h-3" />
        </div>

        {/* Current Step */}
        <div className="space-y-3">
          <div className="flex items-center space-x-2">
            <div className="w-2 h-2 bg-primary rounded-full animate-pulse"></div>
            <span className="text-sm font-medium">
              {steps[currentStep]?.name || "Ä°ÅŸlem tamamlanÄ±yor..."}
            </span>
          </div>
          
          {/* Steps List */}
          <div className="space-y-2">
            {steps.map((step, index) => (
              <div 
                key={index}
                className={`flex items-center space-x-2 text-xs ${
                  index < currentStep 
                    ? 'text-green-600' 
                    : index === currentStep 
                    ? 'text-primary' 
                    : 'text-muted-foreground'
                }`}
              >
                <div className={`w-1.5 h-1.5 rounded-full ${
                  index < currentStep 
                    ? 'bg-green-600' 
                    : index === currentStep 
                    ? 'bg-primary animate-pulse' 
                    : 'bg-muted-foreground/30'
                }`}></div>
                <span>{step.name}</span>
                {index < currentStep && <span className="text-green-600">âœ“</span>}
              </div>
            ))}
          </div>
        </div>

        {/* Time Information */}
        <div className="flex justify-between text-sm text-muted-foreground border-t pt-4">
          <div>
            <span className="font-medium">GeÃ§en SÃ¼re:</span> {formatTime(elapsedTime)}
          </div>
          <div>
            <span className="font-medium">Kalan SÃ¼re:</span> ~{formatTime(remainingTime)}
          </div>
        </div>

        {/* Tips */}
        <div className="bg-muted/50 rounded-lg p-4 space-y-2">
          <h4 className="text-sm font-medium">ðŸ’¡ Ä°pucu</h4>
          <p className="text-xs text-muted-foreground">
            Video oluÅŸturma sÃ¼reci tamamlandÄ±ÄŸÄ±nda size bildirim gÃ¶nderilecek. 
            Bu sÃ¼re zarfÄ±nda sayfayÄ± kapatabilir, daha sonra geri dÃ¶nebilirsiniz.
          </p>
        </div>

        {/* Animated Dots */}
        <div className="flex justify-center space-x-1">
          {[0, 1, 2].map((i) => (
            <div
              key={i}
              className="w-2 h-2 bg-primary rounded-full animate-bounce"
              style={{ animationDelay: `${i * 0.2}s` }}
            ></div>
          ))}
        </div>
      </CardContent>
    </Card>
  )
}
