import { create } from 'zustand'
import { persist } from 'zustand/middleware'

export interface User {
  id: string
  email: string
  name: string
  tier: 'free' | 'premium'
  videosCreatedToday: number
  totalVideosCreated: number
  subscriptionEndDate?: string
  createdAt: string
}

interface UserStore {
  user: User | null
  isAuthenticated: boolean
  setUser: (user: User | null) => void
  updateUserTier: (tier: 'free' | 'premium') => void
  incrementVideoCount: () => void
  resetDailyCount: () => void
  canCreateVideo: () => boolean
  getRemainingVideos: () => number
}

export const useUserStore = create<UserStore>()(
  persist(
    (set, get) => ({
      user: null,
      isAuthenticated: false,

      setUser: (user) => set({ 
        user, 
        isAuthenticated: !!user 
      }),

      updateUserTier: (tier) => set((state) => ({
        user: state.user ? { ...state.user, tier } : null
      })),

      incrementVideoCount: () => set((state) => ({
        user: state.user ? {
          ...state.user,
          videosCreatedToday: state.user.videosCreatedToday + 1,
          totalVideosCreated: state.user.totalVideosCreated + 1
        } : null
      })),

      resetDailyCount: () => set((state) => ({
        user: state.user ? {
          ...state.user,
          videosCreatedToday: 0
        } : null
      })),

      canCreateVideo: () => {
        const { user } = get()
        if (!user) return true // Allow anonymous users (free tier)
        
        if (user.tier === 'premium') return true
        
        // Free tier: 1 video per day
        return user.videosCreatedToday < 1
      },

      getRemainingVideos: () => {
        const { user } = get()
        if (!user) return 1 // Anonymous users get 1 video
        
        if (user.tier === 'premium') return Infinity
        
        // Free tier: 1 video per day
        return Math.max(0, 1 - user.videosCreatedToday)
      }
    }),
    {
      name: 'user-storage',
      partialize: (state) => ({ 
        user: state.user,
        isAuthenticated: state.isAuthenticated 
      }),
    }
  )
)

// Mock user data for development
export const createMockUser = (tier: 'free' | 'premium' = 'free'): User => ({
  id: 'demo_user_123',
  email: 'demo@example.com',
  name: 'Demo User',
  tier,
  videosCreatedToday: 0,
  totalVideosCreated: 0,
  subscriptionEndDate: tier === 'premium' ? new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString() : undefined,
  createdAt: new Date().toISOString()
})

// Video quota management
export interface VideoQuota {
  maxDuration: number
  maxVideosPerDay: number
  hasWatermark: boolean
  allowedQualities: string[]
  allowedThemes: string[]
}

export const getVideoQuota = (tier: 'free' | 'premium'): VideoQuota => {
  if (tier === 'premium') {
    return {
      maxDuration: 120, // 2 minutes
      maxVideosPerDay: Infinity,
      hasWatermark: false,
      allowedQualities: ['standard', 'hd', '4k'],
      allowedThemes: ['scifi', 'nature', 'story', 'poetry', 'abstract', 'documentary']
    }
  }
  
  // Free tier
  return {
    maxDuration: 30, // 30 seconds
    maxVideosPerDay: 1,
    hasWatermark: true,
    allowedQualities: ['standard', 'hd'],
    allowedThemes: ['scifi', 'nature', 'story', 'poetry']
  }
}
