"use client"

import { useState, useRef } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { toast } from 'sonner'

interface VideoPreviewProps {
  videoUrl: string
  title?: string
  duration?: number
}

export function VideoPreview({ videoUrl, title = "OluÅŸturulan Video", duration }: VideoPreviewProps) {
  const [isPlaying, setIsPlaying] = useState(false)
  const [currentTime, setCurrentTime] = useState(0)
  const [videoDuration, setVideoDuration] = useState(duration || 0)
  const [isDownloading, setIsDownloading] = useState(false)
  const videoRef = useRef<HTMLVideoElement>(null)

  const handlePlayPause = () => {
    if (videoRef.current) {
      if (isPlaying) {
        videoRef.current.pause()
      } else {
        videoRef.current.play()
      }
      setIsPlaying(!isPlaying)
    }
  }

  const handleTimeUpdate = () => {
    if (videoRef.current) {
      setCurrentTime(videoRef.current.currentTime)
    }
  }

  const handleLoadedMetadata = () => {
    if (videoRef.current) {
      setVideoDuration(videoRef.current.duration)
    }
  }

  const handleDownload = async () => {
    setIsDownloading(true)
    try {
      const response = await fetch(videoUrl)
      const blob = await response.blob()
      const url = window.URL.createObjectURL(blob)
      const a = document.createElement('a')
      a.href = url
      a.download = `ai-video-${Date.now()}.mp4`
      document.body.appendChild(a)
      a.click()
      window.URL.revokeObjectURL(url)
      document.body.removeChild(a)
      toast.success('Video baÅŸarÄ±yla indirildi!')
    } catch (error) {
      toast.error('Video indirme baÅŸarÄ±sÄ±z')
    } finally {
      setIsDownloading(false)
    }
  }

  const handleShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: title,
          text: 'AI ile oluÅŸturduÄŸum videoyu inceleyin!',
          url: videoUrl,
        })
      } catch (error) {
        // User cancelled sharing
      }
    } else {
      // Fallback: copy to clipboard
      try {
        await navigator.clipboard.writeText(videoUrl)
        toast.success('Video linki panoya kopyalandÄ±!')
      } catch (error) {
        toast.error('Link kopyalama baÅŸarÄ±sÄ±z')
      }
    }
  }

  const formatTime = (time: number) => {
    const minutes = Math.floor(time / 60)
    const seconds = Math.floor(time % 60)
    return `${minutes}:${seconds.toString().padStart(2, '0')}`
  }

  const progressPercentage = videoDuration > 0 ? (currentTime / videoDuration) * 100 : 0

  return (
    <Card>
      <CardHeader>
        <CardTitle>{title}</CardTitle>
        <CardDescription>
          {videoDuration > 0 && `SÃ¼re: ${formatTime(videoDuration)}`}
        </CardDescription>
      </CardHeader>
      <CardContent className="space-y-4">
        {/* Video Player */}
        <div className="relative bg-black rounded-lg overflow-hidden">
          <video
            ref={videoRef}
            src={videoUrl}
            className="w-full h-auto max-h-96"
            onTimeUpdate={handleTimeUpdate}
            onLoadedMetadata={handleLoadedMetadata}
            onPlay={() => setIsPlaying(true)}
            onPause={() => setIsPlaying(false)}
            onEnded={() => setIsPlaying(false)}
          />
          
          {/* Play/Pause Overlay */}
          <div 
            className="absolute inset-0 flex items-center justify-center bg-black/20 opacity-0 hover:opacity-100 transition-opacity cursor-pointer"
            onClick={handlePlayPause}
          >
            <div className="bg-white/90 rounded-full p-4">
              {isPlaying ? (
                <div className="w-6 h-6 flex items-center justify-center">
                  <div className="w-2 h-6 bg-black mr-1"></div>
                  <div className="w-2 h-6 bg-black"></div>
                </div>
              ) : (
                <div className="w-6 h-6 flex items-center justify-center">
                  <div className="w-0 h-0 border-l-[12px] border-l-black border-t-[8px] border-t-transparent border-b-[8px] border-b-transparent ml-1"></div>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Progress Bar */}
        <div className="space-y-2">
          <Progress value={progressPercentage} className="w-full" />
          <div className="flex justify-between text-sm text-muted-foreground">
            <span>{formatTime(currentTime)}</span>
            <span>{formatTime(videoDuration)}</span>
          </div>
        </div>

        {/* Control Buttons */}
        <div className="flex flex-wrap gap-2">
          <Button onClick={handlePlayPause} variant="outline">
            {isPlaying ? 'Duraklat' : 'Oynat'}
          </Button>
          
          <Button 
            onClick={handleDownload} 
            disabled={isDownloading}
            variant="outline"
          >
            {isDownloading ? 'Ä°ndiriliyor...' : 'Ä°ndir'}
          </Button>
          
          <Button onClick={handleShare} variant="outline">
            PaylaÅŸ
          </Button>
          
          <Button 
            onClick={() => {
              if (videoRef.current) {
                videoRef.current.currentTime = 0
                setCurrentTime(0)
              }
            }}
            variant="outline"
            size="sm"
          >
            BaÅŸa Sar
          </Button>
        </div>

        {/* Video Info */}
        <div className="text-sm text-muted-foreground space-y-1">
          <p>â€¢ Video kalitesi: HD (1080p)</p>
          <p>â€¢ Format: MP4</p>
          <p>â€¢ AI ile oluÅŸturuldu</p>
        </div>
      </CardContent>
    </Card>
  )
}
